export function useFetchARECInfo({simulationMode, owner, endDate}:{
    simulationMode: boolean
    owner?:    string
    endDate?:  string
  }): {
    errorAREC:  string
    dataAREC?:  REData[],
    setDataAREC: (dataAREC: REData[] | undefined)=>void
  } {
    const [dataAREC, setDataAREC] = useState<REData[] | undefined>(undefined)
    const [errorAREC, setErrorAREC] = useState('')
    const AREC_URL = useARECURL()

//    console.log('simulationMode, owner, endDate', simulationMode, owner, endDate)
 
    useEffect(() => {

      const fetchData = async () => {
        setErrorAREC('')

        const parameter = {
          jsonrpc: "2.0",
          id: "1",
          method: "rec_getOwnerRecData",
          params: (endDate === undefined) ? {owner: owner} 
                  : { owner: owner, 
                      endDate: DateTime.fromFormat(endDate, "yyyy-MM-dd").toFormat("yyyyMMdd") 
                    }
        }

        try {
          // Default options are marked with *
          const response = await fetch(AREC_URL, {
            mode: 'cors',                                   // no-cors, *cors, same-origin
            method: 'POST',
            headers: {'Content-Type': 'application/json'},
            body: JSON.stringify(parameter) 
          });
          const arecJson = await response.json()
//         console.log('AAAAAAAAAAAA rec_getOwnerRecData', parameter, arecJson, DateTime.now().toISOTime() )
          if(arecJson.error) {
            console.log('Error in rec_getOwnerRecData', parameter, arecJson, DateTime.now().toISOTime() )
          }

          if (arecJson.error) {
            setErrorAREC(arecJson.error.message)
          } else if (arecJson.result) {
            setDataAREC(arecJson.result)
          } else{
            setErrorAREC("Unknown Error!")
          }
        } catch (error) {
          console.log('Error in rec_getOwnerRecData:', error, parameter, DateTime.now().toISOTime())
          setErrorAREC("Network Error!")
        }
      }
      if(simulationMode) return
      if(!owner) {
        setErrorAREC("No Wallet")
      } else {
        fetchData()
      }
    }, [simulationMode, owner, endDate, AREC_URL])
  
    return { errorAREC, dataAREC, setDataAREC }
  }

  export function useIssueOwnerRec(simulationMode: boolean, issueRequest: RequestIssueOwnerRec): {
    errorArecCID:  string
    arecCID?:   RecCID
  } {
    const [arecCID, setArecCID] = useState<RecCID | undefined>(undefined)
    const [errorArecCID, setErrorAREC] = useState('')
    const [deadline, setDeadline] = useState(0)
    const AREC_URL = useARECURL()
 
    useEffect(() => {

      const fetchData = async () => {
        setErrorAREC('')

        const timeout = 30000
        const controller = new AbortController()
        let idTimer = setTimeout(() => controller.abort(), timeout)

        const parameter = {
          jsonrpc: "2.0",
          id: "1",
          method: "rec_issueOwnerRec",
          params: issueRequest,
        }
        try {
          const response = await fetch(AREC_URL, {
            signal: controller.signal,
            mode: 'cors',                                   // no-cors, *cors, same-origin
            method: 'POST',
            headers:  {'Content-Type': 'application/json'},
            body: JSON.stringify(parameter) 
          });
          const arecJson = await response.json()

          if(arecJson.error) {
            console.log('Error in rec_issueOwnerRec', issueRequest, arecJson, DateTime.now().toISOTime() )
          }

          setArecCID(arecJson.result)

        } catch (error) {
          console.log('Error in rec_issueOwnerRec:', error, parameter, DateTime.now().toISOTime())
          setErrorAREC("Network Error!")
        }
        clearTimeout(idTimer)
      }

      if(simulationMode) return
      if(!issueRequest.owner) {
        setErrorAREC("No Wallet")
      } else if(!issueRequest.signatureApproval) {
        setErrorAREC("No Signature")
      } else {
        if( (deadline ===0) || (deadline !== issueRequest?.deadlineApproval) ) {
          setDeadline( issueRequest?.deadlineApproval ?? 0 )
          fetchData()
        }
      }
    }, [simulationMode, issueRequest, deadline, AREC_URL])
  
    return { errorArecCID, arecCID }
  }

  export function useMinerListByOwner(simulationMode: boolean, owner?: string): {
    errorMiner:  string
    minerList?:  MinerInfo[]
  } {
    const [minerList, setMinerList] = useState<MinerInfo[]>([])
    const [errorMiner, setErrorMiner] = useState('')
    const AREC_URL = useARECURL()
 
    useEffect(() => {

      const fetchData = async () => {
        setErrorMiner('')

        const parameter = {
          jsonrpc: "2.0",
          id: "1",
          method: "net_getMinerListByOwner",
          params: {
            "address":owner,
            "offset":1,
            "limit":20
          },
        }
        try {
          const response = await fetch(AREC_URL, {
            mode:   'cors',                                   // no-cors, *cors, same-origin
            method: 'POST',
            headers:  {'Content-Type': 'application/json'},
            body: JSON.stringify(parameter) 
          });
          const minerJson = await response.json()
//          console.log('Json data PPPPPPPPPPPPPPPP', owner, minerJson, DateTime.now().toISOTime() )

          if (minerJson.error) {
            setErrorMiner(minerJson.error.message)
          } else if (minerJson.result === undefined) {
            setErrorMiner("Unknown Error!")
          }
          const miner_list = minerJson.result.map((miner:any) => { 
            const miner_info:MinerInfo =  { address: miner.address, type: miner.minerType, 
                                            maker: miner.maker, power: miner.power,
                                            REOutput: miner.totalEnergyGeneration
                                          }
            return miner_info
          })

          setMinerList(miner_list)
        } catch (error) {
          console.log('Error in net_getMinerListByOwner: ', error, parameter, DateTime.now().toISOTime() )
          setErrorMiner("Network Error!")
        }
      }

      if(simulationMode) return
      if(!owner) {
        setErrorMiner("No Wallet")
      }   
      fetchData()
    }, [simulationMode, owner, AREC_URL])
  
    return { errorMiner, minerList }
  }


  
