function SideNav({ history }: RouteComponentProps) {
  const below1080 = useMedia('(max-width: 1080px)')

//  const below1180 = useMedia('(max-width: 1180px)')

//  const [isDark, toggleDarkMode] = useDarkModeManager()

  return (
    <Wrapper isMobile={below1080}>
      {!below1080 && (
        <DesktopWrapper>
          <AutoColumn gap="1rem" style={{ marginLeft: '.75rem', marginTop: '1.5rem' }}>
            <Title />
            {!below1080 && (
              <AutoColumn gap="0.5rem" style={{ marginTop: '0.5rem' }}>
                <BasicLink to="/Overview" style={{ width: '10.5rem' }}>
                  <Option activeText={history.location.pathname === '/Overview' ||
                                      history.location.pathname === '/Profile' }>
                    <TrendingUp size={18} style={{ marginTop:'0.4rem', marginLeft: '0.6rem', marginRight: '0.5rem' }} />
                    Overview
                  </Option>
                </BasicLink>

                <BasicLink style={{ width: '10.5rem' }} to="/Issuance">
                  <Option activeText={(history.location.pathname === '/Issuance')}>
                    <CheckCircle size={18} style={{ marginTop:'0.4rem', marginLeft: '0.6rem', marginRight: '0.5rem' }} />
                    Issuance
                  </Option>
                </BasicLink>

                <BasicLink style={{ width: '10.5rem' }} to="/Redeem">
                  <Option activeText={history.location.pathname === '/Redeem' ||
                                        history.location.pathname === '/Liquidize' }>                  
                    <LogOut size={18} style={{ marginTop:'0.4rem', marginLeft: '0.6rem', marginRight: '0.5rem' }} />
                    Retirement
                  </Option>
                </BasicLink>

                <BasicLink style={{ width: '10.5rem' }} to="/Offset">
                  <Option activeText={history.location.pathname === '/Offset' ||
                                        history.location.pathname === '/Solidify' }>
                    <Heart size={18} style={{ marginTop:'0.4rem', marginLeft: '0.6rem', marginRight: '0.5rem' }} />
                    Offset
                  </Option>
                </BasicLink>

                <BasicLink style={{ width: '10.5rem' }} to="/mintCert">
                  <Option activeText={(history.location.pathname === '/mintCert')}>
                    <Award size={18} style={{ marginTop:'0.4rem', marginLeft: '0.6rem', marginRight: '0.5rem' }} />
                    Badge
                  </Option>
                </BasicLink>                

                <BasicLink style={{ width: '10.5rem' }} to="/swap">
                  <Option
                    activeText={
                      (history.location.pathname.split('/')[1] === 'swap' ||
                        history.location.pathname.split('/')[1] === 'add') ??
                      undefined 
                  }>
                    <Repeat size={18} style={{ marginTop:'0.4rem', marginLeft: '0.6rem', marginRight: '0.5rem' }} />
                    Exchange
                  </Option>
                </BasicLink>

              </AutoColumn>
            )}
          </AutoColumn>
          <AutoColumn gap="0.2rem" style={{ marginLeft: '.75rem', marginBottom: '4rem' }}>
            <MenuItem id="link" href="https://www.arkreen.com" style={{padding:'0px'}}>
                <Home size={14} style={{ marginLeft: '0.6rem', marginRight: '0.4rem' }} />
                <span style={{fontWeight:400, fontSize:'14px'}}> Arkreen </span>
            </MenuItem>
            <MenuItem id="link" href="https://explorer.arkreen.com/" style={{padding:'0px'}}>
                <List size={14} style={{ marginLeft: '0.6rem', marginRight: '0.4rem' }} />
                <span style={{fontWeight:400, fontSize:'14px'}}> Explorer </span>
            </MenuItem>
            <MenuItem id="link" href="https://docs.arkreen.com" style={{padding:'0px'}}>
                <BookOpen size={14} style={{ marginLeft: '0.6rem', marginRight: '0.4rem' }}/>
                <span style={{fontWeight:400, fontSize:'14px'}}> Docs </span>
            </MenuItem>
            <MenuItem id="link" href="https://github.com/arkreen" style={{padding:'0px'}}>
              <GitHub size={14} style={{ marginLeft: '0.6rem', marginRight: '0.4rem' }}/>
              <span style={{fontWeight:400, fontSize:'14px'}}> Github </span>
            </MenuItem>
            <MenuItem id="link" href="https://twitter.com/arkreen_network" style={{padding:'0px'}}>
                <MessageCircle size={14} style={{ marginLeft: '0.6rem', marginRight: '0.4rem' }}/>
                <span style={{fontWeight:400, fontSize:'14px'}}> Twitter </span>
            </MenuItem>
            </AutoColumn>
        </DesktopWrapper>
      )}
    </Wrapper>
  )
}

export default withRouter(SideNav)


