export function ProfileButton({onClick, title, value, autofocus}: {
  onClick: ()=>void
  title: string
  value?: string
  autofocus?: boolean
}) {
  const theme = useContext(ThemeContext)

  return (
    <ButtonInfo isSelected={autofocus??false} autoFocus={autofocus} style={{padding: '2px'}} onClick={onClick}>
      <AutoColumn gap="2px" style={{ border: '1px,solid'}}>
        <TYPE.body style={{ textAlign: 'center'}}> {title} </TYPE.body>
        <TYPE.body fontSize={24} fontWeight={500} style={{ color:theme.primaryText1, textAlign:'center' }}>
          {value ?? <Loader/>}
        </TYPE.body>
      </AutoColumn>
    </ButtonInfo>   
  )
}


