export function useRECIssuerAddress(): string {
  const [simulationMode] = useSimulationMode()
  const { chainId } = useActiveWeb3React()  

  return useMemo(()=>{  
    return simulationMode ? '0x576Ab950B8B3B18b7B53F7edd8A47986a44AE6F4' 
                          : (chainId === ChainId.MATIC_TESTNET) 
                            ? '0x0AF6Fad1e63De91d5C53Af1dD2e55BB1b278b131' 
                            : '0xec9254677d252df0dCaEb067dFC8b4ea5F6edAfC'
  },[simulationMode, chainId])
}

